import { basic } from '../mixins/basic.js';
export default {
	data() {
		return {
			viewStyle: ''
		}
	},
	mixins:[basic()],
	props:{
		gutter: Number
	},
	watch:{
		gutter: {
			handler (newV, oldV) {
				if(newV && newV!==oldV){
					this.setGutter()
				}
			},
			deep: true
		}
	},
	mounted() {
		if (this.gutter) {
			this.setGutter();
		}	
	},
	methods: {
		setGutter() {
			const { gutter } = this;
			const margin = `-${Number(gutter) / 2}px`;
			const viewStyle = gutter
				? `margin-right: ${margin}; margin-left: ${margin};`
				: '';
			this.setData({ viewStyle });
			this.$children.forEach(col => {
				col.setGutter(this.gutter);
			});
		}
	}
}
